<template>
  <input type="text" id="vue-flatpickr">
</template>

<script>
import Flatpickr from './assets/flatpickr-en.js'

export default {
  props: {
    options: {
      type: Object
    }
  },
  watch: {
    options: {
      handler() {
        this.newFlatpickr()
      },
      deep: true
    }
  },
  ready() {
    this.newFlatpickr()
  },
  mounted() {
    this.newFlatpickr()
  },
  methods: {
    newFlatpickr() {
      new Flatpickr(document.querySelector('#vue-flatpickr'), this.options)
    }
  }
}
</script>



