<template>
  <div id="app">
    <h1>Vue-Flatpickr</h1>
    <p>The date you pick is: {{ date }}</p>
    <vue-flatpickr v-model='date' :options="fpOptions" placeholder="Click Me!"></vue-flatpickr>
    <ul>
      <li>
        <input type="checkbox" id="enableTime" value="true" v-model="fpOptions.enableTime">
        <label for="enableTime">Enable Time</label>
      </li>
      <li>
        <input type="checkbox" id="enableSeconds" value="true" v-model="fpOptions.enableSeconds">
        <label for="enableSeconds">Enable Seconds</label>
      </li>
      <li>
        <input type="checkbox" id="noCalendar" value="true" v-model="fpOptions.noCalendar">
        <label for="noCalendar">No Calendar</label>
      </li>
      <li>
        <input type="checkbox" id="shorthandCurrentMonth" value="true" v-model="fpOptions.shorthandCurrentMonth">
        <label for="shorthandCurrentMonth">Shorthand CurrentMonth</label>
      </li>
      <li>
        <input type="checkbox" id="weekNumbers" value="true" v-model="fpOptions.weekNumbers">
        <label for="weekNumbers">Week Numbers</label>
      </li>
      <li>
        <input type="checkbox" id="time_24hr" value="true" v-model="fpOptions.time_24hr">
        <label for="time_24hr">Time 24 hour</label>
      </li>
      <li>
        <input type="checkbox" id="utc" value="true" v-model="fpOptions.utc">
        <label for="utc">UTC</label>
      </li>
      <li>
        More options in <a href="https://chmln.github.io/flatpickr/#options">Official Document</a>
      </li>
    </ul>
  </div>
  <a href="https://github.com/jrainlau/vue-flatpickr"><img style="position: absolute; top: 0; right: 0; border: 0;" src="https://camo.githubusercontent.com/a6677b08c955af8400f44c6298f40e7d19cc5b2d/68747470733a2f2f73332e616d617a6f6e6177732e636f6d2f6769746875622f726962626f6e732f666f726b6d655f72696768745f677261795f3664366436642e706e67" alt="Fork me on GitHub" data-canonical-src="https://s3.amazonaws.com/github/ribbons/forkme_right_gray_6d6d6d.png"></a>
</template>

<script>
import VueFlatpickr from './vue-flatpickr/VueFlatpickr-en'

export default {
  data() {
    return {
      date: '',
      fpOptions: {
        dateFormat: 'Y-m-d H:i:S K',
        enableTime: false,
        enableSeconds: false,
        noCalendar: false,
        shorthandCurrentMonth: false,
        weekNumbers: false,
        time_24hr: false,
        utc: false
      }
    }
  },
  components: {
    VueFlatpickr
  }
}
</script>

<style>
@import './vue-flatpickr/assets/flatpickr.min.css';
* {
  margin: 0;
  padding: 0;
}
html,
body {
  height: 100%;
  background: #F06292;
  font-family: Microsoft yahei;
  /*color: #fff;*/
}
#app {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
#app h1 {
  font-size: 3em;
  color: #fff;
}
#app p {
  margin: 30px 0;
  color: #fff;
}
#app input[type="text"] {
  margin-bottom: 30px;
  padding: 5px;
  text-align: center;
  border: 2px solid #eee;
  background: none;
  color: #fff;
  outline: none;
}
#app ul li {
  padding: 0;
  color: #fff;
}
#app ul li a {
  color: #fff;
}
::-webkit-input-placeholder { color:#fff; }
::-moz-placeholder { color:#fff; }
:-ms-input-placeholder { color:#fff; }
input:-moz-placeholder { color:#fff; }
</style>



